import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MainShopShcedulePageComponent } from './main-shop-shcedule-page/main-shop-shcedule-page.component';
import { ShopDetailsPageComponent } from './shop-details-page/shop-details-page.component';
import { AppointmentPageComponent } from './appointment-page/appointment-page.component';
import { TimeSchedulePageComponent } from './time-schedule-page/time-schedule-page.component';

const routes: Routes = [
  {
    path: '',
    component: MainShopShcedulePageComponent,
    children: [
      {
        path: '', 
        redirectTo: 'shop-details-list',
        pathMatch: 'full'
      },
      {
        path: "shop-details-list",
        component: ShopDetailsPageComponent,
      },
      {
        path: "appointment-list",
        component: AppointmentPageComponent,
      },
      {
        path: "time-schedule-list",
        component: TimeSchedulePageComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NewShopScheduleRoutingModule { }
